	methods: {
		onPageScroll: function(Object) {
		 //console.log(Object.scrollTop);//实时获取到滚动的值 
		 if(Object.scrollTop > 200){
			this.opacity = Math.abs(Object.scrollTop -200) / 250;
			this.style = {background: `rgba(255,255,255,${this.opacity})`}; 
			this.menu = 1;
		 }else{
